'use client'

import { useEffect, useState } from 'react'

export default function Header() {
	const [scrolled, setScrolled] = useState(false)
	const [loaded, setLoaded] = useState(false)

	useEffect(() => {
		setLoaded(true)

		const handleScroll = () => {
			setScrolled(window.scrollY > 50)
		}
		window.addEventListener('scroll', handleScroll)
		return () => window.removeEventListener('scroll', handleScroll)
	}, [])

	return (
		<header className='w-full h-screen relative overflow-hidden'>
			{/* –ê–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—ã–π –≥—Ä–∞–¥–∏–µ–Ω—Ç–Ω—ã–π —Ñ–æ–Ω */}
			<div className='absolute inset-0 bg-gradient-to-br from-gray-900 via-purple-900 to-violet-900'>
				{/* –ê–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—ã–µ —á–∞—Å—Ç–∏—Ü—ã */}
				<div className='absolute inset-0'>
					{/* –ë–æ–ª—å—à–∏–µ –∫—Ä—É–≥–∏ */}
					<div className='absolute top-1/4 -left-10 w-72 h-72 bg-purple-600/10 rounded-full animate-float-slow blur-3xl'></div>
					<div className='absolute bottom-1/4 -right-10 w-96 h-96 bg-blue-600/10 rounded-full animate-float-delayed blur-3xl'></div>
					<div className='absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-64 h-64 bg-violet-600/10 rounded-full animate-pulse-slow blur-3xl'></div>

					{/* –ú–µ–ª–∫–∏–µ —á–∞—Å—Ç–∏—Ü—ã */}
					<div className='absolute top-1/3 left-1/4 w-2 h-2 bg-white/30 rounded-full animate-twinkle-1'></div>
					<div className='absolute top-2/3 right-1/3 w-1 h-1 bg-blue-300/40 rounded-full animate-twinkle-2'></div>
					<div className='absolute bottom-1/3 left-2/3 w-1.5 h-1.5 bg-purple-300/50 rounded-full animate-twinkle-3'></div>
					<div className='absolute top-1/4 right-1/4 w-1 h-1 bg-cyan-300/40 rounded-full animate-twinkle-4'></div>

					{/* –°–µ—Ç–∫–∞ */}
					<div className='absolute inset-0 opacity-10'>
						<div className='absolute inset-0 bg-gradient-to-r from-transparent via-white to-transparent animate-shimmer-slow'></div>
					</div>
				</div>
			</div>

			{/* –°—Ç–∏–ª–∏ –¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–π */}
			<style jsx>{`
				@keyframes float-slow {
					0%,
					100% {
						transform: translateY(0px) scale(1);
					}
					50% {
						transform: translateY(-20px) scale(1.1);
					}
				}
				@keyframes float-delayed {
					0%,
					100% {
						transform: translateY(0px) scale(1);
					}
					50% {
						transform: translateY(-15px) scale(1.05);
					}
				}
				@keyframes pulse-slow {
					0%,
					100% {
						opacity: 0.1;
						transform: scale(1);
					}
					50% {
						opacity: 0.15;
						transform: scale(1.2);
					}
				}
				@keyframes twinkle-1 {
					0%,
					100% {
						opacity: 0.3;
						transform: scale(1);
					}
					50% {
						opacity: 1;
						transform: scale(1.5);
					}
				}
				@keyframes twinkle-2 {
					0%,
					100% {
						opacity: 0.4;
						transform: scale(1);
					}
					50% {
						opacity: 0.8;
						transform: scale(1.3);
					}
				}
				@keyframes twinkle-3 {
					0%,
					100% {
						opacity: 0.5;
						transform: scale(1);
					}
					50% {
						opacity: 0.9;
						transform: scale(1.4);
					}
				}
				@keyframes twinkle-4 {
					0%,
					100% {
						opacity: 0.3;
						transform: scale(1);
					}
					50% {
						opacity: 0.7;
						transform: scale(1.2);
					}
				}
				@keyframes shimmer-slow {
					0% {
						transform: translateX(-100%);
					}
					100% {
						transform: translateX(100%);
					}
				}
				.animate-float-slow {
					animation: float-slow 8s ease-in-out infinite;
				}
				.animate-float-delayed {
					animation: float-delayed 10s ease-in-out infinite 2s;
				}
				.animate-pulse-slow {
					animation: pulse-slow 6s ease-in-out infinite;
				}
				.animate-twinkle-1 {
					animation: twinkle-1 3s ease-in-out infinite;
				}
				.animate-twinkle-2 {
					animation: twinkle-2 4s ease-in-out infinite 1s;
				}
				.animate-twinkle-3 {
					animation: twinkle-3 5s ease-in-out infinite 2s;
				}
				.animate-twinkle-4 {
					animation: twinkle-4 3.5s ease-in-out infinite 1.5s;
				}
				.animate-shimmer-slow {
					animation: shimmer-slow 8s ease-in-out infinite;
				}
			`}</style>

			{/* NavBar */}
			<NavBar scrolled={scrolled} />

			{/* Hero Content */}
			<div className='container mx-auto px-4 sm:px-6 lg:px-8 h-full relative z-10 pt-20'>
				<div className='grid grid-cols-1 md:grid-cols-2 gap-8 h-full items-center'>
					<TextField loaded={loaded} />
					<ImageField loaded={loaded} />
				</div>
			</div>

			{/* Scroll indicator */}
			<div className='absolute bottom-8 left-1/2 transform -translate-x-1/2 animate-bounce'>
				<div className='w-6 h-10 border-2 border-white/30 rounded-full flex justify-center backdrop-blur-sm'>
					<div className='w-1 h-3 bg-white/70 rounded-full mt-2 animate-pulse'></div>
				</div>
			</div>
		</header>
	)
}
